<template>
    <div class='content'>
        <div ref='stats'></div>
        <div class='con'>
            <div ref='canvas' id='WebGL-output'></div>
        </div>
    </div>
</template>
<script>
import '@/assets/js/TweenMax.min.js';
import mixin from '../mixins/threeMixin';

const mainGroup = new THREE.Group();
export default {
    mixins: [mixin],
    data() {
        return {
            imgArr: [],
            dispArr: [],
            isAnimating: false,
            currentIndex: 1,
            dispIndex: 0,
        };
    },
    methods: {
        initWebGL() {
            const {
                scene,
                camera,
                renderer,
            } = this.initBasics(this.$refs.canvas, { position: { x: 0, y: 0, z: 250 } }, { clearColor: 0xffffff });
            this.scene = scene;
            this.camera = camera;
            this.renderer = renderer;

            this.loadImg();

            const plane = this.addPlane();

            scene.add(mainGroup);

            const clock = new THREE.Clock();
            const renderScene = () => {
                requestAnimationFrame(renderScene);
                renderer.render(scene, camera);
            };

            setTimeout(() => {
                this.showChange();
            }, 1000);

            renderScene();
        },
        addAxes() {
            const axes = this.initAxes();
            mainGroup.add(axes);
        },
        loadImg() {
            for (let i = 0; i < 15; i++) {
                if (i < 5) {
                    const texture = this.loadTexture(require(`../assets/imgs/test/m${i+1}.jpg`));
                    texture.magFilter = texture.magFilter = THREE.LinearFilter;
                    texture.anisotropy = this.renderer.getMaxAnisotropy();
                    this.imgArr.push(texture);
                }
                const disp = this.loadTexture(require(`../assets/imgs/displacement/${i+1}.jpg`));
                disp.wrapS = disp.wrapT = THREE.RepeatWrapping;
                this.dispArr.push(disp);
            }
        },
        addPlane() {
            const geom = this.initGeometry('Plane', 144, 256);
            const mat = this.createMaterial();
            const plane = new THREE.Mesh(geom, mat);
            mainGroup.add(plane);
            return plane;
        },
        createMaterial() {
            const vertexShader = this.initVertexShader();
            const fragmentShader = this.initFragmentShader();

            const uniforms = {
                disFactor: { type: 'f', value: 0.0 },
                currentImage: { type: 't', value: this.imgArr[0] },
                nextImage: { type: 't', value: this.imgArr[3] },
                disp: { type: 't', value: this.dispArr[6] },
            };

            const mat = new THREE.ShaderMaterial({
                uniforms,
                vertexShader,
                fragmentShader,
                transparent: true,
            });

            this.mat = mat;

            return mat;
        },
        showChange() {
            if (this.isAnimating) return;
            this.animation(() => {
                setTimeout(() => {
                    this.currentIndex = this.currentIndex > 3 ? 0 : this.currentIndex + 1;
                    this.dispIndex = Math.floor(Math.random() * 15);
                    this.showChange();
                }, 2000);
            });
        },
        animation(callback) {
            this.isAnimating = true;
            const u = this.mat.uniforms;
            const I = this.currentIndex;
            const DI = this.dispIndex;

            u.nextImage.value = this.imgArr[I];

            TweenMax.to(u.disFactor, 1.6, {
                value: 1,
                ease: Expo.easeInOut,
                onComplete: () => {
                    u.currentImage.value = this.imgArr[I];
                    u.disp.value = this.dispArr[DI];
                    u.disFactor.value = 0.0;
                    this.isAnimating = false;
                    callback && callback();
                },
            });
        },
        initVertexShader() {
            const vs = `
                varying vec2 vUv;
                void main() {
                    vUv = uv;
                    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
                }
            `;
            return vs;
        },
        initFragmentShader() {
            const fs = `
                uniform float disFactor;
                uniform sampler2D currentImage;
                uniform sampler2D nextImage;
                uniform sampler2D disp;
                varying vec2 vUv;
                void main() {
                    vec2 uv = vUv;
                    vec4 dp = texture2D(disp, uv);
                    vec2 distortedPosition1 = vec2(uv.x, uv.y - disFactor * dp.r);
                    vec2 distortedPosition2 = vec2(uv.x, uv.y + (1.0 - disFactor) * dp.r);
                    vec4 texture1 = texture2D(currentImage, distortedPosition1);
                    vec4 texture2 = texture2D(nextImage, distortedPosition2);
                    vec4 finalTexture = mix(texture1, texture2, disFactor);
                    gl_FragColor = finalTexture;
                }
            `;
            return fs;
        },
        addControl() {
            const control = this.initControls('Trackball', this.camera, {
                rotateSpeed: 1.0,
                zoomSpeed: 1.0,
                panSpeed: 1.0,
            });
            return control;
        },
    },
    mounted() {
        this.initWebGL();
    }
};
</script>

<style lang='less' scoped>
.content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255,192,203,0.4);

    .con {
        width: 100%;
        height: 100%;
        position: relative;
    }

    #WebGL-output{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
    }
}
</style>